<template>
  <div class="deu-cookie-banner">
    <PiveauCookieConsent
      elementId="deu-cookie-consent-banner"
      ref="deu-cookie-consent-banner"
      transition-name="slideFromTop"
      type="bar"
      position="top"
      cookie="cookie_consent_drupal"
      cookie-expires="100D"
      :cookie-config="{ acceptValue: '2', declineValue: '0', postponeValue: '1' }"
      :no-cookies-until-consent="false"
      :debug="false"
      :show-postpone-button="false"
      @status="cookieStatus"
      @clicked-accept="cookieClickedAccept"
      @clicked-decline="cookieClickedDecline"
      @clicked-postpone="cookieClickedPostpone"
      @removed-cookie="cookieRemoved"
    >
      <!-- Optional -->
      <template #postponeContent>
        <span class="text-white">
          &times;
        </span>
      </template>

      <!-- Optional -->
      <template #message>
        <!-- TODO improve and translate me -->
        <div class="text-white">
          <div>We use cookies on this site to enhance your user experience</div>
          <div>
            <a
              class="link-more-info text-reset text-decoration-none"
              :href="moreInfoHref"
              target="_self"
            >
              <u>More info</u>
            </a>
          </div>
        </div>
      </template>

      <!-- Optional -->
      <template #declineContent>
        <span class="text-white">
          Accept only essential cookies
        </span>
      </template>

      <!-- Optional -->
      <template #acceptContent>
        <span class="text-dark">
          Accept all cookies
        </span>
      </template>
    </PiveauCookieConsent>
  </div>
</template>

<script>
import PiveauCookieConsent from '@piveau/piveau-cookie-consent';

export default {
  components: {
    PiveauCookieConsent
  },
  props: {
    piwikInstance: {
      type: Object,
      default() {
        return this.$piwik;
      }
    },
    moreInfoHref: {
      type: String,
      default() {
        return `/${this.$i18n?.locale || this.$route?.query?.locale || 'en'}/legal-notice`
      }
    }
  },
  created() {
    if (!this.piwikInstance) console.error('No Piwik instance specified.');
    if (!this.$i18n?.locale && !this.$route?.query?.locale) console.warn('No i18n nor vue-router found. Fallback to "en" locale.')

    window._paq = window._paq || [];
    window.dataLayer = window.dataLayer || [];
    if (this.piwikInstance.type === 'piwikpro') this.initTracker();
  },
  methods: {
    cookieClickedAccept() {
      // Authorize Matomo tracker
      // window._paq.push(['rememberConsentGiven']);
      if (this.piwikInstance.type === 'matomo') this.initTracker();
      this.piwikInstance.consentGiven();
    },
    cookieClickedDecline() {
      if (this.declineJumpToHref) {
        window.open(this.declineJumpToHref, '_blank');
      }
      this.piwikInstance.consentDeclined();
    },
    cookieClickedPostpone() {
      this.piwikInstance.consentNoDecision();
    },
    cookieStatus(status) {
      if (this.piwikInstance.type === 'matomo') {
        if (status === 'accept') {
          this.initTracker();
          this.piwikInstance.consentGiven();
        } else if (status === 'decline') {
          this.piwikInstance.consentDeclined();
        } else if (status === 'postponed') {
          this.piwikInstance.consentNoDecision();
        }
      }
    },
    cookieRemoved() {
      this.piwikInstance.consentDeclined();
    },
    initTracker() {
      this.piwikInstance.init();
    },
  },
}
</script>

